@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
  background-color: #202020;
}

:root {
  --linkcolor: rgb(238, 178, 13);
  --textcolor: rgb(209, 206, 206);
}

.sidebar-top {
  grid-area: sidebar-top;
}

.sidebar-mid {
  grid-area: sidebar-mid;
}

.sidebar-bot {
  grid-area: sidebar-bot;
}

.leftpanel {
  grid-area: leftpanel;
  color: rgb(209, 206, 206);
  color: var(--textcolor);
}

.rightpanel {
  grid-area: rightpanel;
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) {

  .container {
    display: grid;
    height: 100vh;
    grid-template-columns: 4% 46% 50%;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas:
      "sidebar-top leftpanel rightpanel"
      "sidebar-mid leftpanel rightpanel"
      "sidebar-bot leftpanel rightpanel";
  }
  
  .sidebar {
    margin: auto;
  }

  .sidebar ul {
    list-style-type: none;
    margin: auto;
  }

  .sidebar ul li {
    padding: 10px 0;
    margin: auto;
    border-bottom: 1px solid transparent;
  }

  .sidebar ul li span:hover {
    color: rgb(238, 178, 13);
    color: var(--linkcolor);
  }

  .sidebar ul li .active {
    border-bottom: 1px solid rgb(238, 178, 13);
    border-bottom: 1px solid var(--linkcolor);
    color: rgb(238, 178, 13);
    color: var(--linkcolor);
  }

  .sidebar ul {
    margin: auto;
  }

  .sidebar-top {
    margin-top: 0;
  }

  .leftpanel, .rightpanel {
    background-color: #252525;
  }

  .material-icons {
    font-size: 36px;
  }

  .sidebar-bot {
    margin-bottom: 10px;
  }

  .githubicon {
    color: rgb(209, 206, 206);
    color: var(--textcolor);
    font-size: 3vw;
  }

  .githubicon:hover {
    color:rgb(238, 178, 13);
    color:var(--linkcolor);
  }

  .instaimg {
    width: 100%;
  }

  a {
    color: rgb(209, 206, 206);
    color: var(--textcolor);
  }
}

